import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges
} from '@angular/core';
import {CountDownTimerData} from '../model/countDownTimerData';
import {Observable, Subject, Subscription} from 'rxjs';
import {ComponentsUtils} from '../../../common/utils/components-utils';
import {takeUntil} from 'rxjs/operators';

@Component({
  standalone: false,
  selector: 'app-count-down-timer',
  templateUrl: './count-down-timer.component.html',
  styleUrls: ['./count-down-timer.component.scss']
})
export class CountDownTimerComponent implements OnInit, OnChanges, OnDestroy {
  hh: number;
  mm: number;
  ss: number;

  private _onDestroy$ = new Subject<void>();
  private subscription: Subscription;

  @Input()
  countDownData: CountDownTimerData;

  @Input()
  passedSeconds$: Observable<number>;

  @Output()
  deleteClicked = new EventEmitter<void>();

  @Output()
  completed = new EventEmitter<void>();
  isInEditMode: boolean;

  constructor(private readonly cdr: ChangeDetectorRef) {
  }

  ngOnInit(): void {
    // this.subscription = this.passedSeconds$
    //     //   .pipe(
    //     //     takeUntil(this._onDestroy$),
    //     //   )
    //   .subscribe(passedSecond => {
    //     let pendingSeconds = this.countDownData.activationTimer - passedSecond;
    //     this.setTimer(pendingSeconds)
    //     this.cdr.markForCheck();
    //   });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.countDownData) {
      const timer = changes.countDownData.currentValue as CountDownTimerData;
      if (timer) {
        this.setTimer(timer.timerFullDuration);
      }
    }
    if (changes.passedSeconds$) {
      if (this.subscription) {
        this.subscription.unsubscribe();
      }
      const passedSeconds$ = changes.passedSeconds$.currentValue as Observable<number>;
      if (passedSeconds$) {
        this.subscription = passedSeconds$
          .pipe(
            takeUntil(this._onDestroy$),
            takeUntil(this.completed),
          )
          .subscribe(passedSecond => {
            const pendingSeconds = this.countDownData.timerFullDuration - passedSecond;
            this.setTimer(pendingSeconds)
            this.cdr.markForCheck();
            if (pendingSeconds <= 0) {
              this.completed.emit();
              return;
            }
          });
      }
    }

  }

  ngOnDestroy(): void {
    ComponentsUtils.onComponentDestroyed(this._onDestroy$);
  }

  public reset() {
    this.setTimer(this.countDownData.timerFullDuration)
  }

  private setTimer(pendingSeconds: number) {
    if (pendingSeconds < 0) { return; }
    this.ss = pendingSeconds % 60;
    pendingSeconds = Math.round(pendingSeconds / 60);
    this.mm = pendingSeconds % 60;
    pendingSeconds = Math.round(pendingSeconds / 60);
    this.hh = pendingSeconds;
  }

  onEdit() {
    this.isInEditMode = true;
  }

  onEditComplete() {
    this.isInEditMode = false;
  }
}
