import {ChangeDetectorRef, Component, Input, OnDestroy, OnInit, ViewChildren} from '@angular/core';
import {CountDownTimerData} from '../model/countDownTimerData';
import {combineLatest, Observable, Subject, Subscription} from 'rxjs';
import {SecondsTickService} from '../../clock/services/seconds-tick.service';
import {ComponentsUtils} from '../../../common/utils/components-utils';
import {distinctUntilChanged, map, share, startWith, takeUntil} from 'rxjs/operators';
import {CountDownTimerComponent} from '../count-down-timer/count-down-timer.component';

@Component({
  standalone: false,
  selector: 'app-count-down-group',
  templateUrl: './count-down-group.component.html',
  styleUrls: ['./count-down-group.component.scss']
})
export class CountDownGroupComponent implements OnInit, OnDestroy {


  get passedSeconds$() {
    return this.passedSecondsSubject.pipe(share());
  }

  constructor(secondsService: SecondsTickService,
              private readonly cdr: ChangeDetectorRef) {
    this.tickPerSecond$ = secondsService.getSeconds$()
      .pipe(
        takeUntil(this.destroy$),
        map(curSecond => Math.ceil(curSecond / 1000)),
        distinctUntilChanged(),
      );
  }
  private readonly destroy$ = new Subject<void>();
  private readonly onReset$ = new Subject<void>();
  private readonly tickPerSecond$: Observable<number>

  nextId = 0;
  isPlaying = false;
  passedTime = 0;

  @Input()
  runIndividually = true;
  @Input()
  sort = false;
  @Input()
  replay = true;

  @ViewChildren(CountDownTimerComponent)
  childTimerComponents: CountDownTimerComponent[];

  countdownTimers: Array<CountDownTimerData> = [];

  passedSecondsSubject = new Subject<number>();
  runningIndex = 0;

  private subscription: Subscription;

  ngOnInit(): void {
    this.countdownTimers = this.countdownTimers
      .concat(this.createNewTimerData())
      .concat(this.createNewTimerData())
      .concat(this.createNewTimerData())
  }

  ngOnDestroy(): void {
    this.passedSecondsSubject.complete();
    ComponentsUtils.onComponentDestroyed(this.destroy$);
  }

  onAddGroup() {
    this.countdownTimers = this.countdownTimers
      .concat(this.createNewTimerData());
    if (this.sort) {
      this.countdownTimers = this.countdownTimers
        .sort((a, b) => a.timerFullDuration - b.timerFullDuration);
    }
  }

  onDeleteClicked(id) {
    this.countdownTimers = this.countdownTimers.filter(timer => timer.id !== id);
  }

  trackTimerFn(index: number, countDownData: CountDownTimerData) {
    return countDownData.id;
  }

  togglePlay() {
    this.isPlaying = !this.isPlaying;
    const startValue: void = void 0;
    if (this.isPlaying) {
      const observable = this.onReset$.pipe(startWith(startValue));
      this.subscription = combineLatest([this.tickPerSecond$, observable])
        .subscribe(() => this.passedSecondsSubject.next(this.passedTime++));
    } else {
      if (this.subscription) {
        this.subscription.unsubscribe();
      }
    }
  }

  private createNewTimerData() {
    return new CountDownTimerData(this.nextId++, Math.round(Math.random() * (10) ) + 1);
  }

  onTimerCompleted(id: number, index: number) {
    if (this.runIndividually) {
      this.passedTime = 1;
      this.runningIndex++;
      if (this.runningIndex >= this.countdownTimers.length) {
        if (this.replay) {
          this.reset();
        } else {
          return;
        }
      }
      this.cdr.detectChanges();
      this.onReset$.next();
    }
  }

  reset() {
    this.passedTime = 0;
    this.runningIndex = 0;
    this.childTimerComponents.forEach(child => child.reset());
    this.cdr.detectChanges();
    this.onReset$.next();
  }
}
